<template>
	<div class="login">
		<div class="login-box">
			 <el-button style="float:left;margin-right:20px" type="primary" icon="el-icon-back" @click="goback()"></el-button>
			<div class="login-title" style="background-color: #409EFF;text-align: center">
				<span style="line-height: 40px;color: #fff;font-weight: 400;font-size: 25px;text-align: center;">{{getlan('login')}}</span>
			</div>
			<!-- <img v-if="imgLogoState == '0'" src="../assets/login_bg.png" alt="" /> -->
			<div class="form-wrap" style="margin-top: -1px;">
				<form>
		<div class="form-group">
						<label></label>
						<div class="input-group">
						
	<el-radio-group  v-model="tabPositionLogin"    @change="logintypeClick">
	  <el-radio v-model="radio" label="1" key="1"  >{{getlan('login')}}</el-radio>
  <el-radio v-model="radio" label="0"  key="0">{{getlan('register')}}</el-radio>

     </el-radio-group>
						</div>
				</div>

					<div class="form-group">
						<label>{{getlan('username')}}</label>
						<div class="input-group">
							<el-input type="text"  style="text-transform:none;" v-model="formData.username" placeholder=""></el-input>
						</div>
					</div>
					<div class="form-group">
						<label>{{getlan('password')}}</label>
						<div class="input-group">
							<el-input type="password"   style="text-transform:none;" v-model="pwd" placeholder="" ></el-input>
						</div>
					</div>
				<!--	
		    	<div class="form-group" v-show="hiddenRegis">
						<label>{{getlan('nickname')}}</label>
						<div class="input-group">
							<el-input type="text"  style="text-transform:none;" v-model="formData.xingming" placeholder=""></el-input>
						</div>
					</div>
       	           <div class="form-group" v-show="hiddenRegis">
						<label>{{getlan('storeName')}}</label>
						<div class="input-group">
							<el-input type="text"  style="text-transform:none;" v-model="formData.storeName" placeholder=""></el-input>
						</div>
					</div>
				div class="form-group" v-show="hiddenRegis">
						<label>{{getlan('Phone')}}</label>
						<div class="input-group">
							<el-input type="text"  style="text-transform:none;" v-model="formData.phone" placeholder=""></el-input>
						</div>
					</div>
					<div class="form-group" v-show="hiddenRegis">
						<label>{{getlan('Address')}}</label>
						<div class="input-group">
							<el-input type="text"  style="text-transform:none;" v-model="formData.address" placeholder=""></el-input>
						</div>
					</div-->
					<div class="form-group" v-show="hiddenRegis">
						<label>{{getlan('Email')}}</label>
						<div class="input-group">
							<el-input type="text"  style="text-transform:none;" v-model="formData.email" placeholder=""></el-input>
						</div>
					</div>	
					
					<div class="form-group" >
						<label>{{getlan('Verifycode')}}</label>
						<div class="input-group">
													<el-input type="text"  placeholder=""  id="verricode"  v-model="formData.captchaValue" style="width:90px"></el-input>
								<img id="img" v-bind:src="formData.imgurl" @click="getVeriCode" style="width:120px"  alt="code"/>

						</div>
					</div>

						<div class="form-group" v-show="hiddenRole" >
						<label>{{getlan('Role')}}</label>
						<div class="input-group">
	<el-radio-group  v-model="tabPosition"     @change="tabRoleClick">
	  <el-radio v-model="radio" label="1"  v-bind:checked="checked" >{{getlan('Boss')}}</el-radio>
  <el-radio v-model="radio" label="2">{{getlan('Staff')}}</el-radio>
  </el-radio-group>
		</div>
				</div>

					<div class="form-group">
						<el-button v-show="!hiddenRegis" class="btn-block" type="primary" @click="LoginRegister('1')">{{getlan('login')}}</el-button>
						<el-button  v-show="hiddenRegis"   class="btn-block" type="primary" @click="LoginRegister('0')">{{getlan('register')}}</el-button>
					</div>
					
					<div class="form-group">
						<el-checkbox type='checkbox'  :checked="true" @change="handleCheckedChange"> </el-checkbox>
				    <a href="#">Privacy policy</a>
					</div>
						<div class="form-group"  v-show="showloading"  >
					  <el-button style="margin:0 auto;"  icon="el-icon-loading" ></el-button>
					</div>
						<div class="err-msg">{{errMsg}}</div>
				</form>
			</div>
		</div>
	</div>
</template>


<script>
	import Qs from 'qs'
	 import{getDay,getDateTime,getReqUrl, getmd5,getLang,getValue,setValue,decrypt} from "@/utils/pub"
	import{Login,getneedbuy} from "@/api/index"

import { WSANOTINITIALISED } from 'constants';


	export default {
		data() {
			return {
				frompath:'',
				hiddenRole:true,
		        hiddenRegis:false,
				tabPosition:'1',
				tabPositionLogin:"1",
				 checked: true,
				 showloading:false,
				 radio: '1',
				 pwd:'',
				 
				formData:{
					appname:'InvoiceServer',
					captchaValue:'',
					captchaId:'',
					phone:'',
					imgurl:'',
					address:'',
					storeName:'',
					xingming:'',
					username: '',
					email:'',
					password: '',
					table: '',
					role:'1',
				},
				errMsg: '',
			}
		},
		beforeRouteEnter (to, from, next) {
    // 在渲染该组件的对应路由被 confirm 前调用
    // 不！能！获取组件实例 `this`
	// 因为当钩子执行前，组件实例还没被创建
	 next(vm => {
       // 通过 `vm` 访问组件实例,将值传入oldUrl
	   //vm.oldUrl = from.path
		console.log("from.path1="+from.path)
		setValue("frompath",from.path)
     })
  },
		 beforeRouteUpdate(to, from, next){
			  //this.hiddenRegis=true;
     next(vm => {
       // 通过 `vm` 访问组件实例,将值传入oldUrl
	   //vm.oldUrl = from.path
	   if(from.path!=undefined){
           this.frompath=from.path
	   }
		
     })
   },
		  created(){
			  
if(getValue("lan")==undefined){
          this.$i18n.locale = 'zh-CN';
          setValue("lan",'zh-CN')
}

			   if(window.location.host.indexOf(":")>0){
    
        var ip= window.location.host.split(":")[0]
         var port= window.location.host.split(":")[1]
          if ( ip!=null&& ip!=undefined&&ip!='null'){
            setValue('ip', ip);
            setValue('port', port);
         }
		}
		this.frompath=getValue("frompath")
console.log("this.frompath="+this.frompath)

	
			this.getVeriCode();
       if (getValue("username")!=undefined){
    this.formData.username=getValue("username");
			 }
        },
		 methods: {
          getlan(name){ var str=this.$t('lang.'+name+''); if(str.indexOf("lang.")>=0){ return name; }return str },		
				handleCheckedChange(){
				},
				getVeriCode(){
		var puburl="http://"+getReqUrl()+"/"   
		//var	url='//'+window.location.host+"/httpbusiness/getvericode";
			var	url=puburl+"httpbusiness/getvericode"
//this.alert_ok(url)
this.axios.post(url,"").then( (res)=> {
				//	console.log(res);
					var data = res.data.data;
					//this.alert_ok(data.imageUrl)
					this.formData.imgurl=puburl+'httpbusiness/showcodeimg/'+data.imageUrl
				//	this.alert_ok(this.formData.imgurl)
					this.formData.captchaId=data.captchaId
				}).catch( (res)=> {
					this.alert_fail('get verify code error');
				});	
			},
			
		  
			logintypeClick(value){
						 if (value=="0"){
							 this.hiddenRegis=true
							 this.hiddenRole=false;
						 }else{
							 this.hiddenRegis=false
							  this.hiddenRole=true;
						 }
					 },
           tabRoleClick(value){
						 this.formData.role=value;
					 },
				           goback(){
	 this.$router.push({name:'help'});
		  },
		
       
			LoginRegister(type){
			this.errMsg="";
			if (type!='0'&&type!='1'){
				return
			}
				// 输入框验证 hf50 10658999
				if(this.formData.username=='') {
					this.errMsg =this.getlan('please')+" "+this.getlan('input')+" "+this.getlan('username') ;
					this.alert_fail(this.errMsg)
					return;
				}
				
				if(this.pwd=='') {
					this.errMsg = this.getlan('please')+" "+this.getlan('input')+" "+this.getlan('password') ;
					this.alert_fail(this.errMsg)
					return;
				}
					if(this.formData.captchaValue=='') {
					this.errMsg = this.getlan('please')+" "+this.getlan('input')+" "+this.getlan('Verifycode') ;
					this.alert_fail(this.errMsg)
					return;
				} 
					if(this.formData.role=='') {
						this.errMsg = this.getlan('please')+" "+this.getlan('input')+" "+this.getlan('Role') ;
						this.alert_fail(this.errMsg)
					return;
				}
					if (type==0){
					if(this.formData.email=='') {
						this.errMsg = this.getlan('please')+" "+this.getlan('input')+" "+this.getlan('Email') ;
						this.alert_fail(this.errMsg)
							
					return;
				}
			}

				if (type==1){
					this.formData.table="login";
				}else{
					this.formData.table="register";
				}
        this.showloading=true;
	    setValue("username",this.formData.username);
	    this.formData.password=getmd5(this.pwd);
       Login( Qs.stringify(this.formData)).then(res=>{
		   //console.log(res)
				 this.showloading=false;
         
					var map = res.data.message;
					//	console.log(map);
					var code=res.data.code;
					//	this.alert_ok(code)
					if (code==7){
						this.alert_fail(this.getlan('NetworkUnavailable'))
						return
					}
					if (code==22||res.data=="exists"){
						this.alert_fail(this.getlan('username')+" "+this.getlan('Exists'))
						return
					}
					if (code==11){
						this.alert_fail(this.getlan('Verifycode')+" "+this.getlan('wrong'))
					    this.getVeriCode()
						return
					}
					if (this.formData.table=="register"){
						if(code===1){
							this.tabPositionLogin="1";
                            this.hiddenRegis=true;

							this.alert_ok(this.getlan('register')+" "+this.getlan('success')+','+this.getlan('please')+this.getlan('login'));
    // 刷新当前页面
    let NewPage = '_empty' + '?time=' + new Date().getTime()/500;
    this.$router.push(NewPage);
    this.$router.go(-1);
						 this.getVeriCode()
						 this.formData.captchaValue="";
						
						}else{
							this.alert_fail(map);
						}
					}else{
                         if(code===1){
							this.alert_ok(this.getlan('login')+" "+this.getlan('success'));
						
						 }else{
							this.alert_ok(this.getlan('login')+" "+this.getlan('fail'));
						 }
			

		    		if(code ==1) {
						var server_userid=res.data.server_userid;
						setValue('token',res.data.token);
						setValue('userid',res.data.userid);
						setValue('server_userid',server_userid);
						setValue('uid',server_userid);
						setValue('staffid',res.data.staffid);
						setValue('role',res.data.role);
						setValue('power',res.data.power);
						//	this.alert_ok(	getValue('role')+"-"+res.data.role)

	
	

//console.log("this.frompath="+this.frompath)
if(this.frompath=="/shop"){
this.$router.push({name:'shop', query: {userid: getValue("userid_shop")}});
}else{
if (getValue("lan")==undefined){
			 this.$router.push({name:'setting'});
}else{
	
   if(this.frompath!=null&&this.frompath!=undefined){
	    this.$router.push({name:this.frompath.replace(/\//g, '/')});
	   
   }else{
       this.$router.push({name:'help'});
   }
			
}
	 this.$router.go(0)	 
}
 

  

						// if (this.$router.query.redirect) {
						// 	this.$router.push(this.$router.query.redirect);
						// }else{
						// 	this.$store.push('/List')
						// }
		    		} else {
						
								this.alert_fail(res.data.message);
						this.errMsg =res.data.message; 
					}
					}
			 })
			 /*
				this.axios.post("http://127.0.0.1:8081/AjaxFunction/Login.ashx", Qs.stringify(this.formData))
		    	.then( (res)=> {
				
				}).catch( (res)=> {
					this.alert_fail(res.data.message);
				});		
				*/

			}
		}
	}
	

</script>
<style scoped>
	.login{
		position:fixed;
		left: 0;
		top:0;
		right: 0;
		bottom:0;
		background-color: #efefef;
	}
	#LGBtn {
		background:#0091FF;
		border:1px solid #0091FF;
	}
	#LGBtn:hover {
		background:#33A7FF;
		border:1px solid #33A7FF;
	}
	#LGBtn:active {
		background:#0082E5;
		border:1px solid #0082E5;
	}
	
	.login-box{
		width: 600px;
		height: 900px;

		margin:0 auto;
	overflow-x: hidden;
overflow-y: auto;
	}
	.login-title{
		width: 100%;
		height: 40px;
	}
	.form-wrap{
		margin-bottom: 0 !important;
		padding: 25px 70px 20px;
		background-color: #fff;
		border-radius: 0 0 8px 8px;
		height: 900px;
	}
	.form-wrap .form-group{
		margin-bottom: 5px;
		float:left;
		width:100%;
	}
	.form-wrap .form-group label{
		margin-bottom:3px;
	width:100px;
		float:left;
	}
	.form-wrap .form-group .input-group{
		float:left;
		height: 42px;
		line-height: 42px;
	}				
	.form-wrap .form-group .input-group span{
		width: 50px;
		display: block;
		text-align: center;
		height: 40px;
		border:1px solid #d8dce5;
		border-right:none;
		border-radius: 0px 0 0 0px;
	}
	.login-box .err-msg{
		font-size: 12px;
		color: #EF424D;
		letter-spacing: 0.8px;
		line-height: 17px;
		height: 17px;
		text-align: center;
	}
	.login-box .btn-block{
		display: block;
		width: 100%;
		margin: 10px 0 5px 0;
		height: 40px;
	}

	 /*滚动条样式*/
        .login-box::-webkit-scrollbar {/*滚动条整体样式*/
            width: 4px;     /*高宽分别对应横竖滚动条的尺寸*/
            height: 4px;
        }
        .login-box::-webkit-scrollbar-thumb {/*滚动条里面小方块*/
            border-radius: 5px;
            -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
            background: rgba(0,0,0,0.2);
        }
        .login-box::-webkit-scrollbar-track {/*滚动条里面轨道*/
            -webkit-box-shadow: inset 0 0 5px rgba(226, 219, 219, 0.2);
            border-radius: 0;
            background: rgba(212, 209, 209, 0.1);
        }
				
</style>


